<template>
  <view :style="{ paddingTop: marginTop + 'px' }">
    <uv-empty
      :width="width"
      :height="height"
      :text="emptyData.text"
      :icon="emptyData.icon"
      v-bind="$attrs"
    ></uv-empty>
  </view>
</template>

<script>
export default {
  name: 'empty',
  props: {
    width: {
      type: [Number, String],
      default: 160,
    },
    height: {
      type: [Number, String],
      default: 160,
    },
    icon: {
      type: String,
      default: '',
    },
    text: {
      type: String,
      default: '',
    },
    type: {
      type: String,
    },
    marginTop: {
      type: [Number, String],
      default: 90,
    },
  },
  computed: {
    emptyData() {
      return this.getEmptyData(this.type)
    },
  },
  data() {
    return {}
  },
  methods: {
    getEmptyData(type) {
      let obj = {
        error: {
          icon: '错误',
          text: '',
        },
        building: {
          icon: '建设中',
          text: '正在建设中',
        },
        rank: {
          icon: '暂无榜单',
          text: '暂无榜单',
        },
        apply: {
          icon: '暂无报名',
          text: '暂无报名',
        },
        stadium: {
          icon: '暂无场馆',
          text: '暂无场馆',
        },
        order: {
          icon: '暂无订单',
          text: '暂无订单',
        },
        data: {
          icon: '暂无内容',
          text: '暂无内容',
        },
        people: {
          icon: '暂无人员',
          text: '暂无人员',
        },

        collect: {
          icon: '暂无收藏',
          text: '暂无收藏',
        },
        search: {
          icon: '暂无搜索内容',
          text: '暂无搜索内容',
        },
        network: {
          icon: '暂无网络',
          text: '暂无网络',
        },
        message: {
          icon: '暂无消息',
          text: '暂无消息',
        },
        coupon: {
          icon: '暂无优惠券',
          text: '暂无优惠券',
        },
        subscribe: {
          icon: '暂无预约',
          text: '暂无预约',
        },
        info: {
          icon: '暂无资料',
          text: '暂无资料',
        },
        sportCode: {
          icon: '暂无内容',
          text: '暂无权益',
        },
        applyPeople: {
          icon: 'apply-people-empty',
          text: '暂无报名人员',
        },
      }
      let item = obj[type] ? obj[type] : obj['data']
      let iconUrl = `https://cdn-static.papa.com.cn/yuncheng/empty/${item.icon}.png`
      let icon = this.icon ? this.icon : iconUrl
      let text = this.text ? this.text : item.text
      return {
        icon,
        text,
      }
    },
  },
}
</script>

